import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import NoSSR from '@mpth/react-no-ssr';
import { StyledDetails, StyledDetailsImg, StyledDetailsList, StyledDetailsListItem, StyledName } from '../../people/peopleInfo/peopleInfo.styled';
import ErrorMessage from "../../errorMessage/errorMessage";
import Spinner from '../../spinner/spinner';




const PlanetsInfo = ({ selectedPlanet, isFetchingPlanet }) => {
  const [broken, setBroken] = useState(false)

  return (
    // <NoSSR>
    <>
      <StyledDetails>
        {isFetchingPlanet ? <Spinner /> :
          <>
         
         <StyledDetailsImg onError={() => setBroken(true)}
                          src={`https://starwars-visualguide.com/assets/img/planets/${selectedPlanet.id}.jpg`} />
                <>
                  <StyledName>{selectedPlanet.name}</StyledName>
                  
                  {
                    broken ? <ErrorMessage /> :

                        
                        <StyledDetailsList>
                          <StyledDetailsListItem>Population:{selectedPlanet.population}</StyledDetailsListItem>
                          <StyledDetailsListItem>Period: {selectedPlanet.rotation_period}</StyledDetailsListItem>
                          <StyledDetailsListItem>Orbital Period: {selectedPlanet.orbital_period}</StyledDetailsListItem>
                          <StyledDetailsListItem>Diameter: {selectedPlanet.diameter}</StyledDetailsListItem>
                          <StyledDetailsListItem>Climate: {selectedPlanet.climate}</StyledDetailsListItem>
                          <StyledDetailsListItem>Gravity: {selectedPlanet.gravity}</StyledDetailsListItem>
                        </StyledDetailsList>

                  }
                </>

              {/* ) : null} */}
          </>
        }
      </StyledDetails>

    </>
  )
}
const mapStateToProps = (state) => {
  return {
    selectedPlanet: state.selectedPlanet,
    isFetchingPlanet: state.isFetchingPlanet
  }
}



export default connect(mapStateToProps, {})(PlanetsInfo);